.App {
  max-width: 600px;
  margin: 0 auto;
  color: #fff;
}
.header {
  text-align: center;
  background: #000;
}
.subheader {
  background: #000;
  padding: 15px;
  margin-top: 1px;
  color: #ff0;
  font-size: 20px;
}
.board {
  position: relative;
  margin: 1px auto;
}
.cell {
  position: absolute;
  display: inline-block;
  background: #000;
  /* border: .5px solid #eee; */
}
.cell.food {
  background: #000 url('/snake-n-snack/assets/img/snack40x.png') no-repeat 0 0;
  background-size: contain;
}
.cell.snake-body:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: red;
  border-radius: 50%;
}
.cell.snake-body.snake-head:before {
  background: blue;
}
.gamepad .ico {
  color: #00dcff;
  font-size: 30px;
  padding: 5px 10px;
  margin-bottom: 10px;
  text-align: center;
}
.gamepad .group {
  position: absolute;
  display: flex;
  flex-direction: column;
  bottom: 10px;
}
.gamepad .group.left {
  left: 0;
}
.gamepad .group.right {
  right: 0;
}
.status {
  position: absolute;
  top: -33px;
  right: 9px;
  color: orange;
  font-size: 20px;
}
